/* ----------------------------------
   .project 영역 설정
---------------------------------- */
.project {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  height: 100%;
  margin: 0 auto;
}

@media (max-width: 1024px) {
  .project {
    width: 100%;
    height: 100%;
  }
}

/* ----------------------------------
   .pr-box: 전체 박스 스타일
---------------------------------- */
.pr-box {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  gap: 20px;
  position: relative;
  overflow: hidden;
  border-radius: 8px;
}

/* ----------------------------------
   .pr-img: 이미지 컨테이너
---------------------------------- */
.pr-img {
  position: absolute;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100%;
}

/* 이미지 요소 */
.pr-img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

/* ----------------------------------
   .pr-btn: 좌우 버튼 기본 스타일
---------------------------------- */
.pr-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100px;
  height: 100%;
  border: none;
  background-color: transparent;
  color: #aaa;
  font-size: 80px;
  opacity: 0;
  transition: 0.5s;
}

.pr-btn:hover {
  opacity: 1;
}

/* 왼쪽 버튼 위치 설정 */
.btn_L,
.btn_L2,
.btn_L3,
.btn_L4,
.btn_L5,
.btn_L6 {
  left: 0;
}

/* 오른쪽 버튼 위치 설정 */
.btn_R,
.btn_R2,
.btn_R3,
.btn_R4,
.btn_R5,
.btn_R6 {
  right: 0;
}

/* ----------------------------------
   인디케이터 위치 컨테이너
---------------------------------- */
.indis,
.indis2,
.indis3,
.indis4,
.indis5,
.indis6 {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

/* 인디케이터 기본 점 */
.indi,
.indi2,
.indi3,
.indi4,
.indi5,
.indi6 {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #aaa;
  opacity: 0.8;
}

/* 활성화된 인디케이터 점 */
.indi_active,
.indi2_active,
.indi3_active,
.indi4_active,
.indi5_active,
.indi6_active {
  background: #333;
  opacity: 0.8;
}
